import React from 'react'
import { Link } from 'gatsby'

import Layout from '../components/layout'
import SEO from '../components/seo'

const ProjectsPage = () => (
  <Layout>
    <SEO
      title="Projects"
      description="Projects developed for study"
      keywords="React, React JS, React Native, Node, Front-end, Back-end, API, developer"
    />
    <h1>Projects</h1>
    <hr />
    <p>
      Some of my projects, see more on my{' '}
      <a href="https://github.com/leandrodsi">GitHub</a>.
    </p>
    <ul>
      <li>
        <a href="#">Projeto 1</a>
      </li>
      <li>
        <a href="#">Projeto 2</a>
      </li>
    </ul>
    <Link to="/">Go back to the homepage</Link>
  </Layout>
)

export default ProjectsPage
